#banner {
  padding-top: 40px;
  padding-right: 30px;
  box-sizing: border-box;
  position: relative;
  padding-bottom: 80px;
}
#banner .bannerEditBox {
  background: #eceff4;
  padding: 8px 20px;
  position: relative;
}
#banner .bannerEditBox span {
  width: 83px;
  background: #ffffff;
  border: 1px solid #c3c5ce;
  text-align: center;
  padding: 6px 8px;
  display: inline-block;
  font-size: 16px;
  color: #333333;
  cursor: pointer;
  margin-right: 16px;
}
#banner .bannerEditBox .status-active {
  background: #f06e65;
  border: 1px solid #e94035;
  color: #fff;
  margin-right: 16px;
}
#banner .bannerEditBox .add {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  width: 140px;
  color: #E46D64;
  border: 1px solid #c3c5ce;
}
#banner .list {
  padding: 20px 0;
  box-sizing: border-box;
}
#banner .list .flexBox {
  display: flex;
  align-items: center;
}
#banner .list .flexBox .flex {
  flex: 1;
}
#banner .list .list_head,
#banner .list .list_info {
  padding: 10px 0;
  padding-left: 16px;
  box-sizing: border-box;
  border-bottom: 1px solid #f0f0f0;
}
#banner .list .list_head .num,
#banner .list .list_info .num,
#banner .list .list_head .name,
#banner .list .list_info .name,
#banner .list .list_head .status,
#banner .list .list_info .status {
  width: 19%;
  color: #333;
  font-weight: 600;
}
#banner .list .list_head .name,
#banner .list .list_info .name {
  width: 40%;
}
#banner .list .list_head .operation,
#banner .list .list_info .operation {
  color: #333;
  font-weight: 600;
}
#banner .list .list_info .num,
#banner .list .list_info .name,
#banner .list .list_info .status,
#banner .list .list_info .operation {
  font-weight: normal;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
#banner .list .list_info .name {
  width: 40%;
}
#banner .list .list_info .operation a,
#banner .list .list_info .operation > span,
#banner .list .list_info .operation > div {
  display: inline-block;
  background: #f0f0f0;
  border-radius: 2px;
  padding: 6px 8px;
  margin-right: 10px;
  cursor: pointer;
  color: #666666;
  font-size: 14px;
}
#banner .list .list_info .operation a span,
#banner .list .list_info .operation > span span,
#banner .list .list_info .operation > div span {
  padding: 0;
  margin-right: 0;
}
#banner .noData {
  text-align: center;
  margin-top: 20%;
}
#banner .noData img {
  width: 100px;
}
#banner .noData p {
  padding-top: 10px;
  color: #999;
}
#banner .pagination {
  display: flex;
  padding: 10px 0;
  align-content: center;
  justify-content: center;
  position: absolute;
  bottom: 20px;
  width: 100%;
  left: 0;
}
#banner .pagination span {
  display: inline-block;
  padding: 8px;
  cursor: pointer;
}
.addArticle .ant-modal-header {
  background-color: #F1F1F1;
  border: none;
}
.addArticle .ant-modal-title {
  text-align: center;
}
.addArticle .ant-modal-body {
  padding: 32px 52px;
}
.addArticle .ant-modal-body .container {
  display: flex;
}
.addArticle .ant-modal-body .container .ant-tree-iconEle img {
  width: 20px;
  margin-right: 6px;
  vertical-align: middle;
  margin-top: -17.2%;
}
.addArticle .ant-modal-body .container .ant-tree-child-tree img {
  width: 16px;
}
.addArticle .ant-modal-body .container .flex {
  flex: 1;
  overflow: hidden;
}
.addArticle .ant-modal-body .container .title {
  padding-bottom: 20px;
  font-size: 22px;
  color: #333;
}
.addArticle .ant-modal-body .container .search {
  text-align: center;
}
.addArticle .ant-modal-body .container .select .tips {
  font-size: 20px;
  font-weight: 500;
  color: #f1948e;
  padding: 16px 34px;
  box-sizing: border-box;
}
.addArticle .ant-modal-body .container .content {
  background: #f4f6f8;
  border-radius: 4px;
  padding: 16px 0px;
  box-sizing: border-box;
  border: 1px solid #cdcdcd;
  overflow-y: scroll;
  height: 510px;
  overflow-x: hidden;
}
.addArticle .ant-modal-body .container .selected {
  margin-left: 20px;
}
.addArticle .ant-modal-body .container .selected .selectedList {
  position: relative;
}
.addArticle .ant-modal-body .container .selected .second,
.addArticle .ant-modal-body .container .selected .first {
  display: block;
  line-height: 24px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.addArticle .ant-modal-body .container .selected .second i,
.addArticle .ant-modal-body .container .selected .first i {
  color: #f1948e;
  font-size: 18px;
  margin-right: 15px;
  vertical-align: middle;
  margin-top: -1.2%;
}
.addArticle .ant-modal-body .container .selected .second {
  padding-left: 20px;
  cursor: pointer;
}
.addArticle .ant-modal-body .container .selected .icon {
  border: 1px solid #C7C7C7;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  display: inline-block;
  text-align: center;
  font-size: 10px;
  cursor: pointer;
  vertical-align: middle;
  margin-top: -1.2%;
  margin-right: 15px;
}
.addArticle .ant-modal-body .deptList {
  padding: 10px 0;
}
.addArticle .ant-modal-body .deptList .firstDrpt .head {
  background-color: #fff;
  padding: 8px 0;
  padding-left: 10px;
  cursor: pointer;
  box-sizing: border-box;
  color: #666;
  font-size: 16px;
}
.addArticle .ant-modal-body .deptList .firstDrpt .head img {
  width: 20px;
  margin: 0 3px;
}
.addArticle .ant-modal-body .ant-collapse-item-active > .ant-collapse-header {
  background-color: #fff;
}
.addArticle .ant-modal-body .ant-collapse-borderless > .ant-collapse-item {
  border: none;
}
.addArticle .ant-modal-body .ant-collapse-borderless {
  background-color: #f4f6f8;
}
.addArticle .ant-modal-body .ant-collapse-header img {
  width: 20px;
  margin-right: 6px;
}
.addArticle .ant-modal-body .ant-collapse-content-box {
  padding: 0;
  padding-left: 20px;
  box-sizing: border-box;
}
.addArticle .ant-modal-body .ant-collapse-content-box > div > span {
  width: 100%;
  padding: 10px 0;
  display: inline-block;
  padding-left: 20px;
  box-sizing: border-box;
  cursor: pointer;
}
.addArticle .ant-modal-body .ant-input:focus {
  outline: none;
  border: 1px solid #d9d9d9;
  box-shadow: none;
}
.addArticle .ant-modal-body .ant-collapse,
.addArticle .ant-modal-body .ant-collapse-item,
.addArticle .ant-modal-body .ant-collapse-content {
  border: none;
}
.addArticle .ant-modal-body .ant-checkbox-checked .ant-checkbox-inner {
  background-color: #F06E65;
  border-color: #F06E65;
}
.addArticle .ant-modal-body .panal_list {
  display: flex;
  align-items: center;
}
.addArticle .ant-modal-body .panal_list .flex {
  flex: 1;
}
.addArticle .ant-modal-body .btn {
  padding-top: 30px;
  text-align: center;
}
.addArticle .ant-modal-body .btn div {
  display: inline-block;
  width: 112px;
  height: 48px;
  text-align: center;
  line-height: 48px;
  font-size: 20px;
  cursor: pointer;
}
.addArticle .ant-modal-body .btn .send {
  background: #f06e65;
  border-radius: 2px;
  border: 1px solid #ee6258;
  color: #fff;
}
.addArticle .ant-modal-body .btn .close {
  border-radius: 2px;
  border: 1px solid #979797;
  margin-left: 42px;
}
.addBanner .content {
  height: 460px;
  overflow-x: hidden;
}
.addBanner .content img {
  height: 284px;
  display: block;
  margin: 0 auto;
}
.addBanner .content .title {
  color: #333;
  font-size: 24px;
  font-weight: 600;
  padding: 30px 0;
}
.addBanner .tips {
  margin-top: 30px;
  color: #999999;
